
import React from "react";
import { Pane, Heading, Button, Link } from 'evergreen-ui';

// Icons
import { InlineIcon } from '@iconify/react';
import boxIcon from '@iconify/icons-feather/box';

import projects from './ProjectsStore'

const ProjectLink = ({ url, icon }) => (
  <Button
    appearance="minimal"
    is="a"
    href={url}
    margin={16}
    height={32}
  >
    <InlineIcon height={20} icon={icon} />
  </Button>
)

const ProjectBadge = ({ image, link, alt }) => (
  link ? (
    <Link margin={4} href={link}>
      <img src={image} alt={alt} />
    </Link>) : <img src={image} alt={alt} />
)

const Project = ({ project }) => (
  <Pane clearfix>
    <Pane
      elevation={1}
      float="left"
      background="tint2"
      height="15em"
      width="15em"
      margin={24}
      display="flex"
      justifyContent="center"
      alignItems="center"
      flexDirection="column"
    >
      <Pane>
        <Heading margin={16} size={600}>{project.title}</Heading>
      </Pane>
      <Pane>
        {project.badges.map(badge => <ProjectBadge key={badge.img} image={badge.img} link={badge.link} alt={badge.alt} />)}
      </Pane>
      <Pane>
        {project.links.map(link => <ProjectLink key={link.url} url={link.url} icon={link.icon} />)}
      </Pane>
    </Pane>
  </Pane>
)

class Projects extends React.Component {
  render() {
    return (
      <Pane
        flexWrap="wrap"
        wordWrap="break-word"
        padding={16}
      >

        <Pane>
          <Heading size={800} margin="default">
            <InlineIcon icon={boxIcon} width={24} height={24} /> Projects
          </Heading>
        </Pane>

        <Pane
          display="flex"
          flexWrap="wrap"
          justifyContent="center"
          alignItems="center"
          padding={16}
        >
          {projects.map(project => <Project key={project.title} project={project} />)}
        </Pane>
      </Pane>
    )
  }
}

export default Projects